<template>
    <div>
        <h3>src/components/BoardContent.vue</h3>

        <div v-if="state.item">
            글번호 : {{state.no}}<br/>
            제목 : {{state.item.title}}<br />
            내용 : {{state.item.content}}<br />
            작성자 : {{state.item.writer}}<br />
            이미지 : <img :src="state.item.imageurl" style="width:100px" />
            <hr />

            <router-link to="/board"><button>목록으로</button></router-link>
            <button @click="handleDelete">삭제</button>
            <button @click="handleUpdate">수정</button>
            <button v-if="state.item.prev> 0 " @click="handleData(1)">이전글</button>
            <button v-if="state.item.next> 0 " @click="handleData(2)">다음글</button>

            <div v-for="item in state.reply" :key="item">
            {{item._id}}
            내용 :{{item.content}}
            작성자 :{{item.writer}}
            <button @click="handleReplyDelete(item._id)">댓글 삭제</button><br/>
            </div>
            <textarea rows="6" placeholder="댓글내용" v-model="state.reply1.content" ></textarea><br/>
            <input type="text" placeholder="댓글 작성자" v-model="state.reply1.writer" /><br/>
            <button @click="handleReplyAction">댓글 저장</button><br/>
        </div>
    </div>
</template>

<script>
import axios from 'axios'; // 벡엔드연동
import { reactive, onMounted } from 'vue'; // state변수
import { useRoute } from 'vue-router';      // 페이지 이동후 route.query
import { useRouter } from 'vue-router';     // 페이지 이동시킴
export default {
    setup () {
        const route = useRoute();
        const router = useRouter();
        // state변수 생성
        const state = reactive({
            no : route.query.no,  // ?no=123
            reply1 : {
                content : '',
                writer : '',
            }
        });
        const handleReplyDelete = async(no) => {
            const url = `/board/deletereply?_id=${no}`
            const headers = {"Content-Type":"application/json"};
            const response = await axios.delete(url, 
                                {headers:headers, data:{}});
            console.log(response.data);
            if(response.data.status=== 200){
                alert('삭제되었습니다');
                await handleReplyMound(state.no);
                }
        }
        // 댓글쓰기
        const handleReplyAction = async() => {
            const url = `/board/insertreply`;
            const headers = {"Content-Type":"application/json"};
            const body = {
                content : state.reply1.content,
                writer  : state.reply1.writer,
                boardno : state.no
            }
            const response = await axios.post(url, body, {headers:headers});
            console.log(response.data);
            if(response.data.status === 200){
                await handleReplyMound(state.no); //화면 갱신을 위한 메소드
            }
        }

        // 삭제하기 메소드 생성
        const handleDelete = async()=> {
            if(confirm('삭제할까요?')) {
                const url = `/board/delete?no=${state.no}`;
                const headers = {"Content-Type":"application/json"};
                const response = await axios.delete(url, 
                                        {headers:headers, data:{}});
                console.log(response.data);
                if(response.data.status=== 200){
                    router.push({name:"Board", query:{page:1, text:''}})
                }
            }
        }
            const handleMount = async(no) => {
                const url = `/board/selectone?no=${no}`;
                const headers = {"Content-Type":"application/json"};
    
                const response = await axios.get(url, {headers});
                console.log(response.data);
                if(response.data.status === 200){
                    console.log(response.data.result);
                    state.item = response.data.result;
                }
    
            }
        const handleData = async(idx) => {
            if(idx === 1){ //이전글
                // 주소창 변경
                // 게시판 상세화면 -> 게시판상세화면 onmounted 동작x
                router.push({name:"BoardContent", query:{no:state.item.prev}}); 
                state.no = state.item.prev;
                // 누르면 바로갱신될수있도록 await을 넣는다
                await handleMount(state.no);
                await handleReplyMound(state.no);  
            }
            
            else if(idx === 2) { // 다음글
                router.push({name:"BoardContent",query:{no:state.item.next}});
                state.no = state.item.next;
                // 누르면 바로갱신될수있도록 await을 넣는다
                await handleMount(state.no);
                await handleReplyMound(state.no);

            }
        }
        // 댓글
        const handleReplyMound = async() => {
            const url = `/board/selectreply?no=${state.no}`;
            const headers = {"Content-Type":"application/json"};
            const response = await axios.get(url,{headers});
            if(response.data.status === 200){
                console.log(response.data.result);
                state.reply = response.data.result;
            }
            
        }
        // 수정할수 있는 화면으로 전환
        const handleUpdate = async() => {
            router.push({name:"BoardUpdate",query:{no : state.no}});
        //     const url = `/board/insertreply?=no${state.no}`;
        //     const headers = {"Content-Type":"application/json"};
        //     const response = await axios.post(url, body, {headers});
        //     const body = [
        //         body.("title",   state.title),
        //         body.("content", state.content),
        //         body.("writer",  state.writer),
        //     ];
        //      console.log(response.data);
        //         if(response.data.status === 200){
        //             alert('글쓰기 완료');
        //             router.push({name:"Board"});
        // }
        }

        

        // 백엔드 호출
       onMounted( async() => {
           await handleMount(state.no);
           await handleReplyMound(state.no);
        });
        return{state,handleDelete,handleData,handleUpdate,handleReplyAction,handleReplyDelete}
    },
    
}
</script>

<style lang="scss" scoped>

</style>