<template>
    <div class="style1">
        <h3>src/components/Join.vue</h3> <br/>
        {{state}} <hr/>
        이메일 : 
        <el-input v-model="state.userid"  size="mini" style="width:300px" @keyup="handleEmailcheck" ref="userid" /><br/>
        <!-- <input type="text" ref="userid" v-model="state.userid" @keyup="handleEmailcheck"/><br/> -->
        <div>{{state.useremailcheck}}</div><br/>
        암호 : <input type="password"  ref="userpw" v-model="state.userpw"/><br/>
        암호 확인 : <input type="password"  ref="userpw1" v-model="state.userpw1"/><br/>
        이름 : <input type="text"  ref="username" v-model="state.username"/><br/>
        
        권한 : 
        <select v-model="state.userrole">
            <option value="CUSTOMER">고객</option>
            <option value="SELLER">판매자</option>
        </select> <br/>


        <button @click="handleJoin">회원가입</button>
    </div>
</template>

<script>
import { reactive, ref } from 'vue';
import axios from 'axios';
import { useRouter } from 'vue-router';
export default { 
    setup () {
        const router = useRouter();

        // Low 레벨 : 변수 생성 : 오브젝트가 아님/
        const state = reactive({
            userid      : '',
            userpw      : '',
            userpw1     : '',
            username    : '' ,
            useremailcheck : '중복확인',
            userrole     : 'CUSTOMER',
        });


        // v-model 은 내용을 바꿈
        const userid   = ref(null); //위에서 연결하면 bbb값은 의미가 없어짐
        const userpw   = ref(null);
        const userpw1  = ref(null);
        const username = ref(null);
        
        // 정확한 이메일 주소인지 확인
        const validEmail = (email) => {
        // 정규 표현식
        var re = /[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]*$/i;
        return re.test(email);
        };


        const handleEmailcheck = async() => {
            // 입력한 내용이 이메일 형식이면 백엔드로 전송후 중복유무 확인
            if(validEmail(state.userid)){
            console.log(state.userid);
            const url = `/member/emailcheck?email=${state.userid}`;
            const headers = {"Content-Type":"application/json"};
                const response = await axios.get(url, {headers});
                console.log(response.data);
                if(response.data.status === 200){
                    if(response.data.result === 1){
                        state.useremailcheck='사용불가';
                    }
                    else{
                        state.useremailcheck='사용가능'
                    }

            }
        }
        else{
            state.useremailcheck='중복확인';
        }
        }

        //function handleJoin() {}
        const handleJoin = async() => {
            
            if(state.useremailcheck !== '사용가능'){
                alert('이메일 중복체크를 하세요')
                userid.value.focus;
                return false;
            }
            if(state.userid === ''){
                alert('아이디를 입력하세요')
                userid.value.focus();
                return false;
            }
            
            if(state.userpw === ''){
                alert('암호를 입력하세요')
                userpw.value.focus();
                return false;
            }

            if(state.userpw1 === ''){
                alert('암호확인을 입력하세요')
                userpw1.value.focus();
                return false;
            }
            if(state.userpw !== state.userpw1){
                alert('암호가 일치하지 않습니다')
                userpw1.value.focus();
                return false;
            }
            
            if(state.username === ''){
                alert('이름을 입력하세요')
                username.value.focus();
                return false;
            }
            // 유효성 검증완료 되는 시점에 벡엔드 연동

            const url = `/member/insert`;
            const headers = {"Content-Type":"application/json"};
            const body = {
                email    : state.userid, 
                password : state.userpw, 
                name     : state.username,
                role     : state.userrole,
                };
            const response = await axios.post(url, body, {headers});
                if(response.data.status === 200){
                    alert('회원가입되었습니다');
                    router.push({name:'Home'});
                }
            

        }
       

        return {state, userid, userpw, userpw1,
         username, handleJoin,handleEmailcheck}
    }
}
</script>

<!-- scss, less => css -->
<!-- npm install -D sass-loader@^10 sass -->
<style lang="scss" scoped>
    .style1 {
        border: 1px solid #cccccc;
        padding: 20px;
     }

</style>