<template>
    <div>
    <h3>src/components/Menu3.vue</h3>
    암호 입력 : <input type="password" v-model="state.pw"><br/>
    <button @click="handleDeleteAction">회원탈퇴</button>
    </div>
</template>

<script>
import { reactive } from '@vue/reactivity';
import axios from 'axios';
import { useRouter } from 'vue-router';
import { useStore} from 'vuex'
export default {
    setup () {
        const router = useRouter();
        const store = useStore();
        const state = reactive({
            pw : '',
            token : sessionStorage.getItem("TOKEN"),
        })
        const handleDeleteAction = async() => {
            if(confirm('정말 탈퇴할까요?')){
            const url = `/member/delete`;
            const headers = {"Content-Type":"application/json",
                             "token":state.token
            };
            const body = {
                    password : state.pw,
                }
            // 구조) delete(url, {headers:헤드, data:전송값})
            const response = await axios.delete(url, {headers:headers, data:body});
            console.log(response.data);
            if(response.data.status === 200){
                    sessionStorage.removeItem("TOKEN");
                    alert('회원탈퇴 되었습니다.');
                    // 1. 주소창만 바뀜
                    router.push({name:'Home'});
                    // 2. 메뉴활성화 
                    store.commit("setMenu", "/");
                    // 3. 로그인상태
                    store.commit("setLogged", false);
                }
                if(response.data.status === 0){
                    alert('암호가 일치하지 않습니다');
                    return false;
                }
        }}
        

        return {state,handleDeleteAction}
    }
}
</script>

