<template>
    <div>
        <h3>파일명 : src/components/seller/Menu1Detail</h3>
         <router-link to="/seller">목록으로</router-link>
        {{state}}
        <div v-if="state.item">
            코드   : {{state.item._id}}<br />
            물품명 : {{state.item.name}}<br />
            가격   : {{state.item.price}}<br />
            수량   : {{state.item.quantity}}<br />
            내용   : {{state.item.content}}<br />
            등록일 : {{state.item.regdate}}<br />
        

            <img :src="state.item.imageUrl"/> <br/>
            <div v-for="tmp in state.item.subImage" :key="tmp" style="display:inline-block">
                <img :src="tmp.subimageUrl" style="width:150px;height:150px" />
                </div>
        </div>
    </div>
</template>

<script>
import { reactive, onMounted } from 'vue';
import { useRoute } from 'vue-router'; // 페이지 이동시키는 임폴트
import axios from 'axios';
export default {
    setup () {
        const route = useRoute();
        const state = reactive({
            code : route.query.code,
            token : sessionStorage.getItem("TOKEN")
        });

        const handleLoadData = async() => {
            const url = `/seller/selectone?code=${state.code}`;
            const headers = {"Content-Type":"application/json",
                             "token"       :state.token};
            const response = await axios.get(url, {headers});
            if(response.data.status === 200){
                state.item = response.data.result;
                console.log(response.data);
            }
            
        }
        //생명주기
        onMounted( async()=> {
           await handleLoadData();
            
        });
        
        // 생명주기 OnMounted 에서
        // /seller/selectone?code=111을 호출해서 화면에 표시하시오.

        return {state}
    }
}
</script>

