<template>
    <div>
        <h3>Board.vue</h3>
        <!-- <el-table :data="state.items.result" border style="width: 100%" >
            
    <el-table-column prop="no" label="No" width="180" @click="handleBoardContent(no)"></el-table-column>
    <el-table-column prop="title" label="title" width="180" ></el-table-column>
    <el-table-column prop="writer" label="writer" width="180" ></el-table-column>
    <el-table-column prop="hit" label="hit" width="180" ></el-table-column>
    </el-table> -->
    <router-link to="/boardwrite">글쓰기</router-link>
    <table border="1">
            <tr v-for="tmp in state.items.result" :key="tmp">
                <td @click="handleBoardContent(tmp._id)">{{tmp._id}}</td>
                <td>{{tmp.title}}</td>
                <td>{{tmp.writer}}</td>
                <td>{{tmp.hit}}</td>
                <td>{{tmp.regdate}}</td>
            </tr>


        </table>
    
    </div>
</template>

<script>
import {onMounted, reactive} from 'vue';
// 페이지 이동위한 임폴트 보낼때는 router
import {useRouter} from 'vue-router';
import axios from 'axios';

export default {
    setup () { // setup자체가 create
        const router = useRouter();

        const state = reactive({
            items : {},
            page : 1,
            text : ''
        });

        // 생명주기
        onMounted( async() => {
                const url = `/board/select?page=1&text${state.page}&text=${state.text}`;
                const headers = {"Content-Type": "application/json"}
                const response = await axios.get(url, {headers});
                console.log(response.data);

                if(response.data.status === 200) {
                    state.items.result = response.data.rows;
                }

          

        });

            const handleBoardContent = (no) => {
                console.log(no);
                // url - 127.0.0.1:3000/boardcontent?no=2 로 이동
                router.push({name:"BoardContent", query:{no:no}});
            }
        

        return {state, handleBoardContent}
    },
    /*
    date(){
        return{

        }
    }

    */

}
</script>

<style lang="scss" scoped>

</style>