<template>
    <div>
        <h3>파일명 : src/components/Mypage/Menu4.vue</h3>
        {{state}}<br/>
        <button @click="handleOrderDeleteAction">주문취소</button>
        <div v-if="state.items">
        <table border="1">

            <tr>
                <th>물품명</th>
                <th>주문번호</th>
                <th>물품가격</th>
                <th>주문수량</th>
                <th>주문일자</th>
                <th>합계(수량*가격)</th>
            </tr>


            <tr v-for="item in state.items" :key="item">
                <td><input type="checkbox" v-model="state.chk" :value="item._id"/></td>
                <td>{{item.itemname}}</td>
                <td>{{item.itemcode}}</td>
                <td>{{item.itemprice}}</td>
                <td>{{item.ordercnt}}</td>
                <td>{{item.orderdate}}</td>
                <td>{{item.total}}</td>
            </tr>
             <tr>
                    <th colspan="6">합계</th>
                    <td>{{ state.total1 }}</td>
                </tr>

        </table>
        </div>
    </div>
</template>

<script>
import { reactive, onMounted } from 'vue';
import axios from 'axios';
export default {
    setup () {
        const state = reactive({
            //items : handleLoadData() 호출생성
            total1 : 0,
            chk : [],
            token : sessionStorage.getItem("TOKEN")

        })

        const handleOrderDeleteAction = async() => {
            if(state.chk.length === 0){
                    alert('항목을 선택하세요');
                    return false;
                }
            if(confirm('주문 취소 할까요?')){
                const url = `/shop/orderdelete`;
                const headers = {"Content-Type":"Application/json",
                "token" : state.token}

                // data:{ chk : [1,3,4,2 ] }
                // req.body.chk => [1,3,4,2]
                const response = await axios.delete(url,{headers:headers, data:{chk:state.chk}});
                console.log(response.data);
                if(response.data.status === 200){
                    alert('주문취소되었습니다');
                    state.chk = []
                    await handleLoadData();
                }
            }
        }

        onMounted(() => {
            handleLoadData();
        })

        const handleLoadData = async () => { 
            const url = `/shop/orderlist`;
            const headers = {"Content-Type":"Application/json",
                                "token" : state.token};
            const response = await axios.get(url,{headers:headers});
            console.log(response.data);
            if(response.data.status === 200){
                // [ {}, {} , {}
                state.items = response.data.result;

                state.total1 = 0;

                for(let i=0; i<state.items.length;i++){
                    state.items[i]['total'] = Number(state.items[i]['itemprice'])
                                            *Number(state.items[i]['ordercnt']);
                    state.total1 += state.items[i]['total'];
                }
            }
        }
        

        return {state,handleOrderDeleteAction}
    }
}
</script>

