<template>
    <div>
        <h3>src/components/BoardWrite.vue</h3>
        {{state}}<br/>
        제목 : <input type="text" v-model="state.title"/> <br/>
        내용 :
        <div style="width:600px">
         <ckeditor :editor="editor.editor"
                :config="editor.editorConfig"
                v-model="state.content">
                </ckeditor><br/>
                </div>
        작성자 : <input type="text" v-model="state.writer"/><br/>
        이미지 : 
        <img :src="state.imgurl" style="width:80px;height:80px"/>
        <input type="file" @change="hadndleImage"><br/>
        <button @click="handleWrite">글쓰기</button>
    </div>
</template>

<script>
import {reactive} from 'vue';
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import axios from 'axios';
import { useRouter } from 'vue-router';
export default {

    setup () {

        const router = useRouter();
        const editor = {
            //object
                editor  :  ClassicEditor,
                editorData : '<p>테스트</p>',
                editorConfig : {}

        }
        const state = reactive({
            title   : '',
            content : '',
            writer  : '',
            imgdata : '',
            imgurl  : require('../assets/default.png'),
        });
        // 첨부 or 취소 둘다 반응
        const hadndleImage = (e) => {
            console.log(e);
            if(e.target.files[0]){
                state.imgdata = e.target.files[0];
                // 크롬내부에 임시로 이미지를 표시하기 위한 URL생성
                state.imgurl  = URL.createObjectURL(e.target.files[0]);
            }
            else{ // 취소
            state.imgdata = '';
            state.imgurl = require('../assets/default.png');

            }
        };

        const handleWrite = async () => {
            if(state.title === ''){
                alert('제목을 입력하세요')
                return false;
            }
            if(state.content === ''){
                alert('내용을 입력하세요')
                return false;
            }
            if(state.writer === ''){
                alert('작성자을 입력하세요')
                return false;
            }
            if(state.imgdata === ''){
                alert('파일을 선택하세요')
                return false;
            }

            // 유효성 통과후 백엔드 호출하기
                const url = `/board/insert`;
                const headers = {"Content-Type":"multipart/form-data"}
                const body = new FormData(); // 이미지가 있는 경우
                body.append("title",   state.title);
                body.append("content", state.content);
                body.append("writer",  state.writer);
                body.append("image",   state.imgdata);

                const response = await axios.post(url, body, {headers});
                console.log(response.data);
                if(response.data.status === 200){
                    alert('글쓰기 완료');
                    router.push({name:"Board"});
            }
        };
        

        return {state, hadndleImage, handleWrite, editor}
    }
}
</script>

<style lang="css">
    .ck-editor__editable {
        min-height: 500px;
    }
</style>