<template>
    <div>
    <h3>src/components/Menu2.vue</h3>
    현재암호 : <input type="password" v-model="state.pw"> <br/>
    변경암호 : <input type="password" v-model="state.pw1"><br/>
    변경암호 확인 : <input type="password" v-model="state.pw2"><br/>
    <button @click="handleUpdateAction">암호변경</button>
    </div>
</template>

<script>
import { reactive } from 'vue';
import axios from 'axios';

export default {
    setup () {
        const state = reactive({
            pw : '',
            pw1 : '',
            pw2 : '',
            token : sessionStorage.getItem("TOKEN"),
        });

       const handleUpdateAction = async () => {
            if(state.token !== null){

            const url = `/member/updatepw`;
            const headers = {"Content-Type":"application/json",
                             "token":state.token
            };
            const body = {
                password : state.pw,
                password1 : state.pw1
            }
            const response = await axios.put(url,body,{headers});
            console.log(response.data);
            if(response.data.status === 200){
                alert('암호 변경완료')
            }
            if(state.pw === ''){
                alert('현재암호를 입력하세요');
                return false;
            }
            if(state.pw1 === ''){
                alert('변경암호를 입력하세요');
                return false;
            }
            if(state.pw2 === ''){
                alert('변경암호를 입력하세요');
                return false;
            }
            if(state.pw1 !== state.pw2){
                alert('변경암호가 일치하지 않습니다');
                return false;
            }
            if(response.data.status === 0){
                alert('현재암호가 일치하지 않습니다')
                return false;
            }

            }
        }
        

        return {state,handleUpdateAction}
    }
}
</script>

