<template>
    <div>
        <h3>Home.vue</h3>
        <vueper-slides autoplay>
            <vueper-slide v-for="tmp in state.slides"
             :key="tmp"
             :image="tmp.image"
             :title="tmp.title">
             </vueper-slide>
        </vueper-slides>

        <div v-if="state.items" style="margin-top:10px;">
            <el-row :gutter="20" v-for="(i, idx1) in state.items.length/4" :key="i">
                <el-col :span="6" :gutter="10" v-for="(j, idx2) in 4" :key="j">
                    <div style="border:1px solid #cccccc;padding:20px;cursor:pointer;"
                            @click="handleDetailpage(state.items[(idx1 * 4) + idx2]._id)">
                        {{ idx1 }} 
                        {{ idx2 }} 
                        {{ (idx1 * 4) + idx2 }}<br />
                        <img :src="state.items[(idx1 * 4) + idx2].imageUrl" style="width:100%;height:200px;" />
                   물품번호: {{state.items[(idx1 * 4) + idx2]._id}}<br/>
                   이름: {{state.items[(idx1 * 4) + idx2].name}}<br/>
                   가격: {{state.items[(idx1 * 4) + idx2].price}}원<br/>
                   내용: {{state.items[(idx1 * 4) + idx2].content}}<br/>

                    </div>
                </el-col>    
            </el-row>    
        </div>
    </div>
</template>

<script>
import { onMounted, reactive } from '@vue/runtime-core';
import {VueperSlides, VueperSlide} from 'vueperslides';
import 'vueperslides/dist/vueperslides.css';
import axios from 'axios';
import { useRouter } from 'vue-router'; // 페이지 이동시키는 임폴트

export default {
    components : {
        VueperSlides, VueperSlide
    },
    setup () {
        const router = useRouter();

        const state =  reactive({
            
            slides : [
            { title : 'a', image : 'https://picsum.photos/500/300?image=1' },
            { title : 'b', image : 'https://picsum.photos/500/300?image=2' },
            { title : 'c', image : 'https://picsum.photos/500/300?image=3' },
            { title : 'c', image : 'https://picsum.photos/500/300?image=4' },
            { title : 'c', image : 'https://picsum.photos/500/300?image=5' },
            ],
            page : 1,
        
        });
        
        // localhost:3000/itemcontents
        const handleDetailpage = (code) => {
            router.push({name : 'ItemContents',query:{code:code}});
        }

       const handleLoadData = async () => {
            const url = `/shop/select?page=${state.page}`;
            const headers = {"Content-Type":"application/json"};
            const response = await axios.get(url, {headers});
            console.log(response.data);
            if(response.data.status=== 200){
                state.items = response.data.result;

                // 15 % 4 => 3 => 1
                // 14 % 4 => 2 => 2
                // 13 % 4 => 1 => 3
                // 12 % 4 => 0 => 0
                const mod = Math.floor(state.items.length % 4);
                if(mod !== 0 ){
                    for(let i=0;i< 4-mod ;i++){
                        state.items.push({
                            content: "준비중입니다.",
                            imageUrl: require('../assets/default.png'),
                            name: "준비중",
                            price: 0,
                            quantity: 0,
                            seller: "",
                            _id: 0,
                        });
                    }
                }
            }
        }

        onMounted(async()=> {
            await handleLoadData();
        })
       
        

        return {state,handleDetailpage}
    }
}
</script>

<style lang="scss" scoped>
.el-row {
  margin-bottom: 20px;
}
.el-row:last-child {
  margin-bottom: 0;
}
.el-col {
  border-radius: 4px;
}
.bg-purple-dark {
  background: #99a9bf;
}
.bg-purple {
  background: #d3dce6;
}
.bg-purple-light {
  background: #e5e9f2;
}
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
.row-bg {
  padding: 10px 0;
  background-color: #f9fafc;
}
</style>