// npm install vuex@next --save
import {createStore} from "vuex";
import axios from "axios";

//모든 컴포넌트에서 공통으로 사용할 변수설정하기
// props와 emit를 여기에서 처리함.
const stores= createStore({

    //공통 상태 변수
    state : {
        menu :  sessionStorage.getItem("CURL"), // 선택되는 메뉴
        logged : false, // 로그인 상태
        uid    : '',    // 로그인한 사용자의 이메일정보
        uname  : '',    // 로그인한 사용자의 이름
        token  : '',    // 토큰을 저장소에 보관하지않고 사용
        urole   : '',    // 로그인한 사용자의 권한을 저장
    }, 
    // 가져가기 (getter)
    getters : {
        getUid(state){
            return state.uid;
        },
        getUname(state){
            return state.uname;
        },
        getMenu(state){
            return state.menu;
        },
        getLogged(state){
            return state.logged;
        },
        getUrole(state){
            console.log('store/getUrole', state.urole);
            return state.urole;
        }
    },
    // 변경하기 (mutation) : 즉시 변경
    mutations : {
        setUid(state, val){
            state.uid = val;
        },
        setUname(state, val){
            state.uname = val;
        },
        setMenu(state, value){
            state.menu = value;
        },
        setLogged(state, value){
            state.logged = value;
        },
        setUrole(state, value){
            console.log('store/setUrole', value);
            state.urole = value;
        },

    },
    // 변경하기(action) : 기다려야 되는 상황
    actions : {
        async handleData(context, payload){
            console.log(payload);
            const token = sessionStorage.getItem("TOKEN");
            if (typeof token !== 'undefined' && token !== null) {
                const url = `/member/validation`;
                const headers = {
                    "Content-Type" : "application/json",
                    "token" : token
                }
                const response  = await axios.get(url, {headers:headers});
                console.log('store/index.js/handData', response.data);
                if(response.data.status === 200){
                    // mutations의 setUid, setUname을 호출해서 내용변경
                    context.commit("setUid", response.data.uid);
                    context.commit("setUname", response.data.uname);
                    context.commit("setUrole", response.data.urole);
                    context.commit("setLogged", true);
                }
                else{
                    // 토큰의 유효성을 검사하여 통과하지 못할경우 로그아웃시킨다
                    context.commit("setLogged", false);

                }
            }
        }
    }
});

export default stores;
