<template>
    <div>
        <h3>Login.vue</h3>
        {{state}}
                <el-form>
                <el-form-item label="아이디" label-width="120px" >
                    <el-input v-model="state.userid"  size="mini" placeholder="아이디" style="width:192px"></el-input>
                </el-form-item>
                <el-form-item label="패스워드" label-width="120px">
                    <el-input v-model="state.userpw" size="mini" placeholder="아이디" style="width:192px"></el-input>
                </el-form-item>
                 <el-button value="로그인" @click="handleLogin" style="margin-left:120px">로그인</el-button>
                </el-form>
        
    </div>
</template>

<script>
import { reactive } from 'vue'
import axios from 'axios';
import { useRouter } from 'vue-router';
import { useStore} from 'vuex'

export default {
    // ver 3.0 이 더 보기 편함
    setup () { // this를 사용할 수 없음
        const store = useStore();
        const router = useRouter();
        const state = reactive({
            userid : 'c@com',
            userpw : 'c'
        });

        const handleLogin = async() =>{
            const url = `/member/select`;
            const headers = {"Content-Type":"application/json"};
            const body = {
                email : state.userid,
                password : state.userpw
            };
            const response = await axios.post(url, body,{headers});
            console.log(response.data.token);
            
            
            if(response.data.status === 200){
                alert('로그인 되었습니다');
                console.log('login.vue=>',response.data);
                // 저장소에 보관하기(공통변수) 크롬창을 닫아도 유지됨 (로컬스토리지)    
                sessionStorage.setItem("TOKEN", response.data.token); 
               
                // 3. 로그인 상태
                store.commit("setLogged",true);
                // acions를 호출하여 store/state변수를 변경함
                store.dispatch("handleData",{})
                
                const curl = sessionStorage.getItem("CURL");
                if(curl === null){
                    // 1. 주소창만 바뀜
                    router.push({name:'Home'})
                    // 2. 메뉴 활성화
                   store.commit("setMenu","/");

                    }
                else {  // 이동하고자하는 페이지가 존재하면
                    // string => object로 변경
                    const query  =JSON.parse(sessionStorage.getItem("CRUL_QUERY"))
                    const params =JSON.parse(sessionStorage.getItem("CRUL_PARAMS"))
                    router.push({name:curl, query:query, params:params})
                }

                // App.vue에 메뉴의 선택항목을 변경하도록 알려줌
                // 실시간을 바로 알려주는 역할
                
                
            }
            if(response.data.status !== 200){
                alert('아이디 혹은 암호가 일치하지않습니다')
            }
          
        };

        return {state,handleLogin}
    }
}


// state 변수 ver 2.0
// data(){
//     return{


//     }

</script>

